import { Card, Image, Button, Alert } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import {
  useNavigate,
} from "react-router-dom";

const CompleteComponent = () => {

  let navigate = useNavigate();
  let orderInfo = [];
  let orderProductArr = [];
  let order = useSelector((state) => {
    return state.order;
  });

  if (typeof order === "undefined" || order.length===0) {

  } else {
    orderInfo = order.orderInfo;
    orderProductArr = orderInfo.orderProductArr;
  }

    return (
      <>
        {order.length === 0 ? (
          <div>
            <Alert variant="danger">
              <Alert.Heading>    .</Alert.Heading>
              <p>
                  .
                <br />
                    .
                <br />
                <Alert.Link href="/"></Alert.Link>   
                .
              </p>
              <hr />
              <p className="mb-0"> : 02) 000-0000</p>
            </Alert>
            <div className="d-grid gap-2 mt-3">
              <Button
                type="button"
                variant="danger"
                size="lg"
                onClick={() => {
                  navigate("/");
                }}
              >
                HOME
              </Button>
            </div>
            <div className="mt-3"></div>
          </div>
        ) : (
          <div style={{ position: "absolute", top: "20%" }}>
            <h1 style={{ textAlign: "center" }}> .</h1>
            <Card className="mt-3">
              <Card.Body>
                <blockquote className="blockquote mb-0">
                  <footer className="blockquote-footer mt-3">
                     :{" "}
                    <cite title="Source Title">
                      <span style={{ color: "red", fontWeight: "600" }}>
                        {order.orderNo}
                      </span>
                    </cite>
                  </footer>
                  <footer className="blockquote-footer mt-3">
                     :{" "}
                    <cite title="Source Title">
                      <span style={{ fontWeight: "700" }}>
                        {orderInfo.name}
                      </span>
                    </cite>
                  </footer>
                  <footer className="blockquote-footer mt-3">
                     :{" "}
                    <cite title="Source Title">
                      <span style={{ fontWeight: "700" }}>{orderInfo.hp}</span>
                    </cite>
                  </footer>
                  <footer className="blockquote-footer mt-3">
                     :{" "}
                    <cite title="Source Title">
                      <span style={{ fontWeight: "700" }}>
                        {orderInfo.addr} {orderInfo.addrDtl}
                      </span>
                    </cite>
                  </footer>
                  <footer className="blockquote-footer mt-3"></footer>
                  <Card border="primary" className="mt-3">
                    <Card.Body>
                      {orderProductArr &&
                        orderProductArr.map((it, index) => (
                          <div className="mt-3" key={index}>
                            <Card.Title>
                              <Image
                                src={`/image/${it.productFileName}`}
                                thumbnail
                                width="20%"
                              />
                              &nbsp;{it.productTitle}
                            </Card.Title>
                            <Card.Text>
                               :{" "}
                              <span style={{ color: "red" }}>
                                {Number(it.productPrice) *
                                  Number(it.productcount)}
                              </span>
                              ( : {it.productcount})
                            </Card.Text>
                            <Card.Text> : {it.productSize}</Card.Text>
                          </div>
                        ))}
                    </Card.Body>
                  </Card>
                </blockquote>
              </Card.Body>
            </Card>
            <div className="d-grid gap-2 mt-3">
              <Button
                type="button"
                variant="primary"
                size="lg"
                onClick={() => {
                  navigate("/");
                }}
              >
                HOME
              </Button>
            </div>
            <div className="mt-3"></div>
          </div>
        )}
      </>
    );
};

export default CompleteComponent;
